import { uuidv7 } from "uuidv7";
import * as Paho from 'paho-mqtt';
import { BROKER } from './Constants';


export var MqttClient = function() {

  this.isConnected = false;
  this.clientId = undefined;
  this.client = null;
  this.topicCallback = [];
  this.onConnectCallback = undefined;
  this.keepAliveInterval = undefined;

  this.useNativeReconnectMethod = false;
  this.customReconnectInterval = undefined;
  this.customReconnectTimeout = null;

  // Connexion du client MQTT
    this.connect = function(connectionOptions={}) {
    if (!this.isConnected) {
      let h = connectionOptions.host ? connectionOptions.host : BROKER.HOST;
      this.clientId = 'MQTT_MEDIA_UPLOADER_' + uuidv7();
      this.client = new Paho.Client(h, this.clientId);
      this.keepAliveInterval = connectionOptions.keepAliveInterval? connectionOptions.keepAliveInterval : 15;   // (en secondes)
      this.useNativeReconnectMethod = connectionOptions.useNativeReconnectMethod? connectionOptions.useNativeReconnectMethod : false; 
      this.customReconnectInterval = connectionOptions.customReconnectInterval? connectionOptions.customReconnectInterval : 10;   // (en secondes)
      this.customReconnectInterval = this.customReconnectInterval*1000;     //  (then cast to millis)
      this.client.onMessageArrived = this.onMessageArrived.bind(this);
      this.client.onConnectionLost = this._onConnectionLost.bind(this);
      if (connectionOptions.callback) this.onConnectCallback = connectionOptions.callback;
      // Tentative de connexion
      this.client.connect({ 
        reconnect: this.useNativeReconnectMethod? true : false,
        keepAliveInterval: this.keepAliveInterval,
        onSuccess: this.onConnect.bind(this), 
        onFailure: this.onConnectFailure.bind(this) 
      });
    }
  }.bind(this);

  // Déconnexion du client MQTT
  this.disconnect = function() {
    if (this.client) this.client.disconnect();
    // if(!this.useNativeReconnectMethod){
      clearTimeout(this.customReconnectTimeout); // Annuler la reconnexion si déconnexion manuelle
      this.customReconnectTimeout = null;
    // }
    this.isConnected = false;
    this.topicCallback = [];
  };

  // Lors de la perte de connexion
  this._onConnectionLost = function(responseObject) {
    this.isConnected = false;
    if (responseObject.errorCode !== 0)
      console.log('_onConnectionLost:' + responseObject.errorMessage);
    this.topicCallback.forEach(function(elem) {
      elem.isSubscribed = false;
    });
    if(this.onConnectionLost)
      this.onConnectionLost(responseObject);

    // Tentative de reconnexion toutes les N secondes
    if(!this.useNativeReconnectMethod){
      if (this.customReconnectTimeout === null) {
        this.customReconnectTimeout = setTimeout(() => {
          console.log('Tentative de reconnexion...');
          this.connect(); // Reconnexion
        }, this.customReconnectInterval);
      }
    }
  };

  // Lors de la réussite de la connexion
  this.onConnect = function() {
    console.log('Connected to broker!');
    this.isConnected = true;
    // if(!this.useNativeReconnectMethod){
      clearTimeout(this.customReconnectTimeout); // Annuler la reconnexion si la connexion réussit
      this.customReconnectTimeout = null;
    // }
    if (this.onConnectCallback) this.onConnectCallback();

    var client = this.client;
    this.topicCallback.forEach(function(elem) {
      if (!elem.isSubscribed) {
        elem.isSubscribed = true;
        console.log('Re-subscribing to topic: ', elem.topic);
        client.subscribe(elem.topic);
      }
    });
  };

  // Lors d'un échec de connexion
  this.onConnectFailure = function() {
    console.log('Échec de la connexion !');
    // Si la connexion échoue, essayer à nouveau après N secondes
    if(!this.useNativeReconnectMethod){
      console.log('Tentative de reconnexion...');
      setTimeout(() => {
        this.connect(); // Reconnexion
      }, this.customReconnectInterval);
    }
  };

  // Lors de la réception d'un message
  this.onMessageArrived = function(message) {
    var payloadString = message.payloadString;
    var topic = message.destinationName;
    var t = this.topicCallback.find(elem => {
      return elem.topic === topic;
    });
    if (t) {
      t.callbacks.forEach(function(callback) {
        callback(topic, payloadString);
      });
    }
  };

  // Publier un message
  this.publish = function(topic, message, qos=0, retained=false) {
    this.client.publish(topic, message, qos, retained);
  };

  // S'abonner à un sujet
  this.subscribe = function(topic, callback) {
    if(!topic) 
      return;
    console.log('Subscribing to topic: ', topic);
    var t = this.topicCallback.find(tcb => {
      return tcb.topic === topic;
    });
    if (t) {
      t.callbacks.push(callback);
    } else {
      var topic_cb = {};
      topic_cb.topic = topic;
      topic_cb.callbacks = [callback];
      topic_cb.isSubscribed = this.isConnected;
      this.topicCallback.push(topic_cb);
      if(this.client && this.isConnected)
          this.client.subscribe(topic);
    }
  };

  // Se désabonner d'un sujet
  this.unsubscribe = function(topic) {
    if(!topic) return;
    if (this.isConnected) {
      var newCallbacks = this.topicCallback.filter(function(element) {
        return topic !== element.topic;
      });
      this.topicCallback = newCallbacks;
      this.client.unsubscribe(topic);
    }
  };
};
