import { useRef, useCallback, useState, useEffect } from 'react';
import { Grid, Button, Mask, DotLoading } from 'antd-mobile';
import { CameraOutline, CloseOutline, LoopOutline } from 'antd-mobile-icons';
import Webcam from 'react-webcam';
import imageCompression from 'browser-image-compression';
import { isMobile } from 'react-device-detect';

import { isOrientationPortrait } from './common/Utils';

import flashlightIcon from './media/flashlight_white.png';
import './CameraView.css';


// const screenshotFormat = "image/jpeg";
// const screenshotFormat = "image/png";
const screenshotFormat = "image/webp";


function CameraView(props) {

  const webcamRef = useRef(null);
  const [isPortrait, setIsPortrait] = useState(false);
  const [useFrontCamera, setUseFrontCamera] = useState(false);
  const [showWebcam, setShowWebcam] = useState(false);
  const [showProcessingMask, setShowProcessingMask] = useState(false);
  const [useFlash, setUseFlash] = useState(false);

  useEffect(() => {
    // console.log("I have been mounted [CameraView]")
    setUseFrontCamera( isMobile? false : true );
    updateOrientationValue();
    window.addEventListener('resize', updateOrientationValue);
    return () => {
      window.removeEventListener('resize', updateOrientationValue);
    }
  }, [])

  const updateOrientationValue = () => {
    setIsPortrait(isOrientationPortrait());
    resetWebcam();
  }

  const resetWebcam = () => {
    setShowWebcam(false);
    setTimeout(()=>setShowWebcam(true), 500);
  }

  const toggleFlash = async () => {
    const stream = webcamRef.current?.video?.srcObject;
    if (stream) {
      const videoTrack = stream.getTracks()[0]; // Récupérer le track vidéo
      const capabilities = videoTrack.getCapabilities();
      
      if (capabilities.torch) {
        // Changer l'état de la torche
        await videoTrack.applyConstraints({
          advanced: [{ torch: !useFlash }],
        });
        setUseFlash(!useFlash);
      }
    }
  };

  const captureImage = useCallback(
    () => {
      setShowProcessingMask(true);
      // return;

      setTimeout(()=>{
        const imageSrc = webcamRef.current.getScreenshot();
        let imageSrcCompressed = undefined;
        // console.log("captureImage", imageSrc);
        const image = new Image();
        image.src = imageSrc;
        image.onload = async () => {
          try {
            // Créer un canvas et y dessiner l'image
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            canvas.width = image.width;
            canvas.height = image.height;
            ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
            // Convertir le contenu du canvas en Blob
            canvas.toBlob(async (blob) => {
              const options = {
                maxSizeMB: 1.5,
                // maxSizeMB: 0.5, // Taille maximale du fichier (en Mo)
                // maxWidthOrHeight: 800, // Largeur ou hauteur maximale
                useWebWorker: true, // Utilisation du web worker pour la compression
              };
              const compressedImageBlob = await imageCompression(blob, options);
              imageSrcCompressed = await blobToBase64(compressedImageBlob);
              // console.log("imageSrcCompressed", imageSrcCompressed);
              setShowProcessingMask(false);
              if(props.onImageCaptured)
                props.onImageCaptured(imageSrcCompressed);
            }, screenshotFormat);
          } 
          catch (error) {
            setShowProcessingMask(false);
            console.error('Erreur lors de la compression de l\'image:', error);
          }
        };
        // if(props.onImageCaptured)
        //   props.onImageCaptured(imageSrc);
      }, 500);
    },
    [webcamRef]
  );

  const blobToBase64 = (blob) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => resolve(reader.result);
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
  };

  return (
    <div className='camera-view'>
        <div className={"main-window " + (isPortrait? 'portait' : 'paysage')}>

          <div className='camera-preview-container'>
            {
              showWebcam &&
              <Webcam
                audio={false} 
                width="100%" height="100%"
                width__={ isPortrait? "100%" : undefined }
                height__={ isPortrait? "100%" : undefined }
                ref={webcamRef}
                screenshotFormat={screenshotFormat}
                imageSmoothing={true}
                screenshotQuality={1}
                forceScreenshotSourceSize={true}
                minScreenshotWidth_={ isPortrait? 2480 : 3508 }
                videoConstraints={{
                  facingMode: { exact: useFrontCamera? "user" : "environment" },
                  //aspectRatio: 1.5859,
                  aspectRatio: window.innerWidth>window.innerHeight?
                      window.innerWidth/window.innerHeight : window.innerHeight/window.innerWidth,
                    // width: 1280,
                    // height: 720,
                    // width: 800,
                    // height: 600,
                    // width: { min: 640, ideal: 1920, max: 1920 },
                    // height: { min: 400, ideal: 1080 },
                    // width: { min: 600, max: 1920 },
                    // height: { min: 600, max: 1920 },
                    // width: { min: 600, max: 1920, ideal: isPortrait?1080:1920 },
                    // height: { min: 600, max: 1920, ideal: isPortrait?1920:1080 },
                    width: { min: 600, max: 2480, ideal: 1920 },
                    height: { min: 600, max: 2480, ideal: 1920 },
                }} 
              />
            }
          </div>

          <Grid columns={0} gap={6} className='footer-btns-container responsive'>
              <Grid.Item>
                <Button size='large' block className='app-btn transparent' onClick={captureImage}>
                  <CameraOutline />
                </Button>
              </Grid.Item>
              <Grid columns={2} gap={6}>
                <Grid.Item>
                  <Button size='large' block className='app-btn transparent' onClick={toggleFlash}
                    style={{background:useFlash? '#fffb0044' : undefined}}>
                    <img src={flashlightIcon} alt='' />
                  </Button>
                </Grid.Item>
                <Grid.Item>
                  <Button size='large' block className='app-btn transparent' onClick={()=>{
                    setUseFlash(false);
                    setUseFrontCamera(!useFrontCamera);
                  }}>
                    <LoopOutline />{false && <span className='label'>Camera</span>}
                    </Button>
                </Grid.Item>
              </Grid>
              <Grid.Item>
                <Button size='large' block className='app-btn transparent' onClick={()=>{
                  if(props.onCancelBtnClick)
                      props.onCancelBtnClick();
                }}>
                  <CloseOutline color='var(--adm-color-danger)' /><span className='label'>Annuler</span>
                </Button>
              </Grid.Item>
            </Grid>

            <Mask visible={showProcessingMask} opacity={0.85}>
              <div className="mask-overlay-content">Optimisation de l'image <DotLoading /></div>
            </Mask>

        </div>
      </div>
  );
}

export default CameraView;
