import { useEffect } from 'react';
import { setDefaultConfig } from 'antd-mobile';
import frFR from 'antd-mobile/es/locales/fr-FR';

import DocumentCaptureView from './DocumentCaptureView';

import './App.css';

function App() {
  useEffect(() => {

    // console.log("I have been mounted [App]")
    setDefaultConfig({
      locale: frFR,
    });

    const enableWakeLock = async () => {
      if ('wakeLock' in navigator) {
        try {
          await navigator.wakeLock.request('screen');
          console.log("L'écran est maintenu allumé");
        } catch (err) {
          console.error("Erreur lors de la demande du Wake Lock : ", err);
        }
      } else {
        console.log('L\'API Screen Wake Lock n\'est pas supportée');
      }
    };

    enableWakeLock();

  }, [])

  return (
    <DocumentCaptureView />
  );

}

export default App;
