import { useState, useEffect } from 'react';
import { Grid, Button, Image  } from 'antd-mobile';
import { CloseOutline, SendOutline } from 'antd-mobile-icons';

import { isOrientationPortrait } from './common/Utils';

import './ImageView.css';


function ImageView(props) {

  const [isPortrait, setIsPortrait] = useState(false);

  useEffect(() => {
    // console.log("I have been mounted [CameraView]")
    window.addEventListener('resize', updateOrientationValue);
    updateOrientationValue();
    return () => {
      window.removeEventListener('resize', updateOrientationValue);
    }
  }, [])

  const updateOrientationValue = () => {
    setIsPortrait(isOrientationPortrait());
  }

  return (
    <div className='image-view'>
        <div className={"main-window " + (isPortrait? 'portait' : 'paysage')}>

          <div className='image-preview-container'>
            <Image src={props.imageData}  width="100%" height="100%" fit='contain' />
          </div>

          <Grid columns={0} gap={6} className='footer-btns-container responsive'>
              <Grid.Item>
                <Button size='large' block className='app-btn transparent' onClick={()=>{
                  if(props.onSendImageBtnClick)
                      props.onSendImageBtnClick();
                }} loading={props.uploadingImageData}>
                  <SendOutline /><span className='label'>Envoyer</span>
                </Button>
              </Grid.Item>
              <Grid.Item>
                <Button size='large' block className='app-btn transparent' onClick={()=>{
                  if(props.onCancelBtnClick)
                      props.onCancelBtnClick();
                }}>
                  <CloseOutline color='var(--adm-color-danger)' /><span className='label'>Annuler</span>
                </Button>
              </Grid.Item>
            </Grid>

        </div>
      </div>
  );
}

export default ImageView;
