import { useState, useEffect, useRef } from 'react';
import { Grid, Button, NoticeBar, Dialog, ImageUploader, Mask, DotLoading, Toast } from 'antd-mobile';
import { 
    CameraOutline, UploadOutline, CloseOutline, 
    SmileOutline, ArrowsAltOutline, ShrinkOutline 
} from 'antd-mobile-icons';
import { isMobile } from 'react-device-detect';
import axios from 'axios';

import {MqttClient} from './common/MqttClient';
import { BROKER } from './common/Constants';
import { CATBOX_API } from './common/Constants';
import { aesEncrypt } from './common/Utils';

import CameraView from './CameraView';
import ImageView from './ImageView';

import logo from './media/Logo_Munisys.png';
import logo2 from './media/cloud-computing.png';
import './DocumentCaptureView.css';
import { uuidv7 } from 'uuidv7';
import CryptoJS from 'crypto-js';

const PROCESS_STEP_IMAGE_CAPTURING = "IMAGE_CAPTURING";
const PROCESS_STEP_IMAGE_UPLOADING = "IMAGE_UPLOADING";

const IsMunisysBuild = false;

function DocumentCaptureView() {

    const [mqttClient, setMqttClient] = useState(undefined);
    const [topicUploadDataName, setTopicUploadDataName] = useState(undefined);
    const [topicImageDataName, setTopicImageDataName] = useState(undefined);
    const [encryptionKey, setEncryptionKey] = useState(undefined);
    const [brokerStatus, setBrokerStatus] = useState(0);
    const [processStep, setProcessStep] = useState(undefined); 
    const [imageData, setImageData] = useState(undefined); 
    const [uploadingImageData, setUploadingImageData] = useState(false); 
    const [fullscreen, setFullscreen] = useState(false);
    const [fileToUpload, setFileToUpload] = useState(undefined);
    const [uploadingFile, setUploadingFile] = useState(false);
    const uploaderInput = useRef(null)

    useEffect(() => {
        // console.log("I have been mounted [DocumentCaptureView]")
        const queryParameters = new URLSearchParams(window.location.search)
        const caller = queryParameters.get("caller");
        const token = queryParameters.get("token");
        const key = queryParameters.get("key");
        if(!caller || !token) {
            //throw new Error("Paramétres d'entrée non trouvés: 'caller' et/ou 'token'");
            console.error("Paramétres d'entrée non trouvés: 'caller' et/ou 'token'");
            return;
        }
        // console.log(caller, token, key);
        const topicPrefix = BROKER.TOPIC_PREFIX+'/'+caller+'/'+token;
        // console.log("topicPrefix", topicPrefix);
        const _topicStatusName = topicPrefix + '/upload-data';
        const _topicImageDataName = topicPrefix + '/image-data';
        setTopicUploadDataName(_topicStatusName);
        setTopicImageDataName(_topicImageDataName);
        setEncryptionKey(key);

        const handleVisibilityChange = () => {
            if (document.hidden) {
                // Si la page devient invisible (onglet perdu ou réduite), vous pouvez la laisser en pause ou gérer la déconnexion
                console.log("Page is not visible, consider pausing MQTT activities...");
            } else {
                // Lorsque la page redevient visible (onglet ou fenêtre récupérés)
                console.log("Page is visible, attempting to reconnect to MQTT...");
                initBroker();
            }
        };
        // document.addEventListener("visibilitychange", handleVisibilityChange);

        setTimeout(()=>initWebcam());
        setTimeout(()=>initBroker());

        return () => {
            // document.removeEventListener("visibilitychange", handleVisibilityChange);
            disconnectBroker();
        }
    }, [])

    const initWebcam = () => {
        navigator.mediaDevices.getUserMedia({ video: { facingMode: { exact: (isMobile? "environment" : "user") } } })
            .then(function (stream) {
                stream.getTracks().forEach(function(track) {
                    track.stop();
                });
            }.bind(this))
            .catch(function (error) {
                console.error("ERROR ACCESSING VIDEO STREAM: ", error);
                Dialog.alert({
                    content: 'Erreur camera: ' + error.toString(),
                    onConfirm: () => {},
                })
            }.bind(this));
    }

    const initBroker = () => {
        try{
            if(mqttClient && mqttClient.isConnected) return;
            console.log("Initializing broker connection ...");
            const _mqttClient = mqttClient? mqttClient : new MqttClient();
            if(_mqttClient){
                _mqttClient.onConnectCallback = () => {
                    console.log("connected to broker");
                    // alert("connected to broker");
                  setBrokerStatus(1);
                //   Dialog.confirm({
                //     content: "Voulez-vous activer le mode 'Plein écran'? (vous pouvez basculer à tout moment en cliquant sur le bouton en haut à droite de l'écran)",
                //     onConfirm: () => {
                //         goFullScreen();
                //     },
                //   });
                //   setTimeout(()=>{
                //     publishMessage(topicStatusName, "DEVICE_CONNECTED");
                //   }, 1000);
                }
                _mqttClient.onConnectionLost = (responseObject) => {
                    console.log('onConnectionLost', responseObject);
                    setBrokerStatus(0);
                }
                // _mqttClient.subscribe("/adiop/test",(topic,message)=>{        // FOR TESTING ...
                //     console.log("on msg received: ", topic, message);
                // });
                _mqttClient.onMessageArrived = (message) => {
                    console.log('onMessageArrived', message);
                }
                _mqttClient.connect({ keepAliveInterval: 45, customReconnectInterval: 5 });
                setMqttClient(_mqttClient);
              }
          }
          catch (error) {
            console.error('Erreur initialisation client MQTT', error);
          }
    }

    const disconnectBroker = () => {
        console.log("Disconnecting broker ...")
        if(mqttClient){
            try{
                if(mqttClient.isConnected)
                    mqttClient.disconnect();
                setMqttClient(undefined);
                console.log("Broker disconnected !")
            }
            catch (error) {
                console.error('Erreur initialisation client MQTT', error);
            }
        }
        else console.log("Cannot disconect broker : mqttClient is not defined");
        setTopicUploadDataName(undefined);
        setTopicImageDataName(undefined);
        setEncryptionKey(undefined);
        setBrokerStatus(2);
        exitFullScreen();
    }

    const publishMessage = (topic, message) => {
        if(mqttClient){
            try{
                // console.log("Publishing message", topic, message);
                mqttClient.publish(topic, message);
            }
            catch (error) {
                console.error('Erreur client MQTT', error);
            }
        }
        else console.error("Cannot publish msg : mqttClient is not defined", topic, message);
    };

    const exitApp = () => {
        Dialog.show({
            content: 'Etes vous sûr de vouloir terminer ?',
            closeOnAction: true,
            actions: [
              [
                {
                    key: 'annuler',
                    text: 'Annuler',
                },
                {
                    key: 'terminer',
                    text: 'Terminer',
                    danger: true,
                    onClick: () => {
                        disconnectBroker();
                    },
                },
              ],
            ],
          });
    }

    const toggleFullscreen = () => {
        if(fullscreen){
            exitFullScreen();
        }
        else {
            goFullScreen();
        }
    }

    const goFullScreen = () => {
        if(!document.fullscreenElement){
            document.body.requestFullscreen();
            setFullscreen(true);
        }
    }

    const exitFullScreen = () => {
        if(document.fullscreenElement){
            document.exitFullscreen();
            setFullscreen(false);
        }
    }

    const handleTakePictureBtnClick = () => {
        // console.log("handleTakePictureBtnClick");
        // const msg = "Publish msg ...";
        // publishMessage(topicImageDataName, msg);
        // return;
        goFullScreen();
        setProcessStep(PROCESS_STEP_IMAGE_CAPTURING);
    };

    const handleSendImageBtnClick = () => {
        // console.log("SendBtnClick");
        setUploadingImageData(true);
        setTimeout(()=>{
            // console.log("imageData", imageData);
            const message = encryptionKey? aesEncrypt(imageData, encryptionKey) : imageData;
            const imageId = uuidv7();
            // publishMessage(topicImageDataName, message);                    
            let charIndex = 0;             
            const maxLength = 300000;
            const chunksArray = [];
            while (charIndex < message.length) {
                let chunk = message.substring(charIndex, charIndex + maxLength);
                chunksArray.push(chunk);
                charIndex += maxLength; 
            }
            publishMessage(topicImageDataName, imageId + '[#:#:#]' + chunksArray.length); 
            for (let idx=0; idx<chunksArray.length; idx++) {
                publishMessage(topicImageDataName, imageId + '[#:#:#]' + idx + '[#:#:#]' + chunksArray[idx]); 
            }
            setTimeout(()=>{
                setImageData(undefined);
                setProcessStep(undefined);
                setUploadingImageData(false);
            }, 5000);
        });
    };

    const openFileUploadDialog = () => {
        const nativeInput = uploaderInput.current?.nativeElement
        if (nativeInput) {
          nativeInput.click()
        }
    }

    const uploadFile = async () => {
        if(!fileToUpload) return;
        // console.log("fileToUpload", fileToUpload);
        let _fileToUpload = fileToUpload;
        if(encryptionKey){
            const encryptedFile = await encryptFile(fileToUpload, encryptionKey);
            _fileToUpload = new Blob([encryptedFile], { type:fileToUpload.type });
        }


        // var a = document.createElement("a");
        // var url = window.URL.createObjectURL(_fileToUpload);
        // var filename = fileToUpload.name + ".enc";
        // a.href = url;
        // a.download = filename;
        // a.click();
        // window.URL.revokeObjectURL(url);

        // decrypt(_fileToUpload, encryptionKey, fileToUpload.name);


        // return;


        const formData = new FormData();
        // if(encryptionKey)
        //     formData.append('fileToUpload', new Blob([encryptedFile], { type:fileToUpload.type }), fileToUpload.name);
        formData.append('fileToUpload', _fileToUpload, fileToUpload.name);
        formData.append('reqtype', 'fileupload');
        formData.append('time', CATBOX_API.EXPIRES);
        setUploadingFile(true);
        axios({ 
            url: CATBOX_API.URL, 
            method: 'POST',
            data: formData, 
        }).then(response => {
            const result = response.data;
            // console.log("response", result);
            setFileToUpload(undefined);
            setUploadingFile(false);
            Toast.show({
                icon: 'success',
                content: "Fichier envoyé !",
                duration: 4000,
            });
            const payload = {
                id: uuidv7(),
                name: fileToUpload.name,
                size: fileToUpload.size,
                type: fileToUpload.type,
                link: result
            };
            publishMessage(topicUploadDataName, JSON.stringify(payload)); 
        }).catch(error => {
            console.error(error.message, error);
            setUploadingFile(false);
            displayUploadErrorMessage();
        });
    }

    const displayUploadErrorMessage = () => {
        Toast.show({
            icon: 'fail',
            content: "Erreur lors de l'envoi du fichier !",
            duration: 4000,
        });
    }

    const encryptFile = (file, uuid) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onload = function(event) {
                // const fileData = event.target.result; // Données binaires du fichier (ArrayBuffer)
                // const wordArray = CryptoJS.lib.WordArray.create(fileData); // Conversion en format crypto-js
                const wordArray = CryptoJS.lib.WordArray.create(reader.result); // Conversion en format crypto-js
                const encrypted = CryptoJS.AES.encrypt(wordArray, uuid).toString(); // Crypter avec l'UUID
                resolve(encrypted); // Retourner les données cryptées
            };
            reader.onerror = function(error) {
                reject(error); // Gérer l'erreur de lecture
            };
            reader.readAsArrayBuffer(file); // Lire le fichier comme ArrayBuffer (binaire)
        });
    }


    // const convertWordArrayToUint8Array = (wordArray) => {
    //     var arrayOfWords = wordArray.hasOwnProperty("words") ? wordArray.words : [];
    //     var length = wordArray.hasOwnProperty("sigBytes") ? wordArray.sigBytes : arrayOfWords.length * 4;
    //     var uInt8Array = new Uint8Array(length), index=0, word, i;
    //     for (i=0; i<length; i++) {
    //         word = arrayOfWords[i];
    //         uInt8Array[index++] = word >> 24;
    //         uInt8Array[index++] = (word >> 16) & 0xff;
    //         uInt8Array[index++] = (word >> 8) & 0xff;
    //         uInt8Array[index++] = word & 0xff;
    //     }
    //     return uInt8Array;
    // }

    // const decrypt = (file, key, filename) => {
    //     var reader = new FileReader();
    //     reader.onload = () => {
    
    //         var decrypted = CryptoJS.AES.decrypt(reader.result, key);               // Decryption: I: Base64 encoded string (OpenSSL-format) -> O: WordArray
    //         var typedArray = convertWordArrayToUint8Array(decrypted);               // Convert: WordArray -> typed array
    
    //         var fileDec = new Blob([typedArray]);                                   // Create blob from typed array
    
    //         var a = document.createElement("a");
    //         var url = window.URL.createObjectURL(fileDec);
    //         // var filename = file.name.substr(0, file.name.length - 4);
    //         a.href = url;
    //         a.download = filename;
    //         a.click();
    //         window.URL.revokeObjectURL(url);
    //     };
    //     reader.readAsText(file);
    // }

    return (
      <div>
        <div className="main-window">

        {
            IsMunisysBuild &&
            <img src={logo} className="app-logo" alt="logo" />
        }

        {
            !IsMunisysBuild &&
            <img src={logo2} className="app-logo-2" alt="logo" />
        }

        <div className='app-title'>
            Mobile Media Uploader
        </div>

        {
            (brokerStatus===0 || !mqttClient || !mqttClient.isConnected) && brokerStatus!==2 &&
            <NoticeBar
                color='alert'
                shape='neutral'
                bordered icon=''
                content="Reconnexion en cours ..." />
        }

        {
            brokerStatus===1 &&
            <Grid columns={3} gap={6} className='footer-btns-container'>
                <Grid.Item>
                    <Button size='large' block className='app-btn' onClick={handleTakePictureBtnClick}>
                        <CameraOutline /><span className='label'>Prendre photo</span>
                    </Button>
                </Grid.Item>
                <Grid.Item>
                    <ImageUploader ref={uploaderInput} preview={false} showUpload={false} accept="*/*" beforeUpload={(file, files) => setFileToUpload(file)} />
                    <Button size='large' block className='app-btn' onClick={openFileUploadDialog}>
                        <UploadOutline /><span className='label'>Charger fichier</span>
                    </Button>
                </Grid.Item>
                <Grid.Item>
                    <Button size='large' block className='app-btn' onClick={exitApp}>
                    <CloseOutline color='var(--adm-color-danger)' /><span className='label'>Terminer</span>
                    </Button>
                </Grid.Item>
            </Grid>
        }

        {
            brokerStatus===2 &&
            <NoticeBar
                color='info' size="large"
                shape='neutral'
                bordered icon={<SmileOutline />}
                content="Merci, à bientot" />
        }

        </div>

        {
            processStep===PROCESS_STEP_IMAGE_CAPTURING &&
            <CameraView 
                onCancelBtnClick={()=>setProcessStep(undefined)}
                onImageCaptured={(imageData)=>setImageData(imageData)} />
        }

        {
            imageData &&
            <ImageView 
                imageData={imageData} uploadingImageData={uploadingImageData}
                onCancelBtnClick={()=>setImageData(undefined)}
                onSendImageBtnClick={handleSendImageBtnClick} />
        }

        <Dialog
            visible={fileToUpload!==undefined}
            content={fileToUpload? "Voulez-vous envoyer le fichier sélectionné '"+fileToUpload.name+"' ("+fileToUpload.type+")" : ""}
            actions={[
                [{
                    key: 'annuler',
                    text: 'Annuler',
                    danger: true,
                    onClick: () => {
                        setFileToUpload(undefined);
                    },
                },
                {
                    key: 'envoyer',
                    text: 'Envoyer',
                    onClick: uploadFile,
                }],
            ]}
        />

        <Mask visible={uploadingFile} opacity={0.85}>
            <div className="mask-overlay-content">Transfert du fichier en cours <DotLoading /></div>
        </Mask>

        <Button className='go-fullscreen-btn' onClick={toggleFullscreen}>
            { fullscreen? <ShrinkOutline /> : <ArrowsAltOutline /> }
        </Button>
      </div>
    );
  }
  
  export default DocumentCaptureView;